@import '../variables/colors';
@import '../variables/font';
@import '../abstracts/invertColor';
@import '../elements/input';
@import '../elements/button';
.CitySearch {
  padding:2em;
  color: $color-lighter;
  &__err{
    color:green;
    font-size: 3rem;
    z-index: -1;
    position: absolute;
    margin:1em 0;
    min-height: 100%;
    top:7em;
    max-width:100%;
  }
  &__label{
    font-size: $font-4;
    display:block;
    color:$color-light;
    margin-bottom:.4em;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
  
  &__btn {
    @extend button-element;
  }
  
  input{
    @extend input-element;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
}

.suggestions {
  li{
    list-style: none;
  }
}