@import '../variables/font';
@import '../variables/colors';


.Event{
  //min-width:100%;
  background-color: $color-lighter;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, .09) 0px -3px 5px;
  border-radius: 7px;
  padding: 1.7em;
  margin: .8em 0;
  list-style-type: none;
  -webkit-border-radius:7px;
  -moz-border-radius:7px;
  -ms-border-radius:7px;
  -o-border-radius:7px;
    @include respond-to(lg-screen){
    max-width: 40vw;
  }
  @include respond-to(tablet-portrait){
    padding:.7em;
    max-width: 70vw;
  }
  @include respond-to(mobile-portrait){
    max-width: 100vw;
  }
}

.event{
  &-details-hide{
    display:none;
    
  }
  &-details-show{
    display:block;
    height:100%;
  }
  
  &-title{
    font-size:3.5rem;
    margin-bottom: 1.2em;
    cursor: pointer;
    // font-weight:200;
    font-style: italic;
    @include respond-to(sm-screen){
      font-size:1.8em;
    }
    @include respond-to(xs-screen){
      font-size:1em;
    }

    &-container{
      display:flex;
      align-items: baseline;
      justify-content:space-between;
      @include respond-to(tablet-portrait){
        flex-flow:column;
        align-items: flex-end;
        margin-bottom: 1em;;
      }
    }
  }
  
  &-description{
    font-size: $font-1;
  }
  &-date, &-location{
    font-weight: 800;
  }
  
  &-link{
    font-style: italic;
    text-decoration: none;
    display:inline-block;
    margin: 2em 0;
    overflow:hidden;
    color: $color-main-1;
    word-wrap: break-word;
    max-width: 100%;
    cursor: pointer;
    &:hover{
      color:$color-main;
    }
  }
}